import request from '../utils/request'

/**
 * 获取全部公告
 * @param params {{size: number, page: number}}
 * @returns  公告分页
 */
export function getBillBoardListApi(params){
    return request({
        url:"/billboard/getList",
        method:"get",
        params
    })
}
/**
 * 添加公告
 * @param data {{content : String}} 公告内容
 * @returns
 */
export function addBillBoardApi(data){
    return request({
        url:"/billboard/add",
        method:"post",
        data
    })
}
/**
 * 删除公告
 * @param id : Number 公告id
 * @returns 删除的公告
 */
export function deleteBillBoardApi(id){
    return request({
        url:"/billboard/delete",
        method:"delete",
        params:{id}
    })
}
export function updateBillBoardShowApi(id){
    return request({
        url:"/billboard/updateShow",
        method:"put",
        params:{id}
    })
}

/**
 * 获取全部标签
 * @param params {{size: number, page: number}}
 * @returns  标签分页
 */
export function getAllTagsApi(params){
    return request({
        url:"/tag/getAllTags",
        method:"get",
        params
    })
}

/**
 * 删除标签
 * @param id 标签id
 * @returns 删除的标签
 */
export function deleteTagApi(id){
    return request({
        url:"/tag/deleteTag",
        method:"delete",
        params:{id}
    })
}
