<template>
  <div class="app-container">
    <el-button type="primary" style="margin-bottom: 10px"
               @click="dialogAddVisible = true">添加
    </el-button>
    <el-table
        ref="filterTable"
        :data="tableData"
        v-loading="listLoading"
        border
        style="width: 100%">
      <!--编号-->
      <el-table-column
          prop="id"
          label="编号"
          sortable
          width="100px"
      >
      </el-table-column>
      <!--标签-->
      <el-table-column
          prop="content"
          label="公告">
      </el-table-column>
      <!--话题数量-->
      <el-table-column
          prop="show"
          label="是否展示">
        <template slot-scope="{row}">
          <el-tooltip :content="row['show']?'展示':'不展示'" placement="top">
            <el-switch
                v-model="row['show']"
                @change="switchControl($event,row)">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <!--操作-->
      <el-table-column
          label="操作"
          width="180">
        <template slot-scope="scope">
          <el-button type="danger" size="mini" icon="el-icon-delete"
                     @click.native.prevent="deleteRow(scope.$index, tableData,scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-show="total>0" :total="total"
                :page.sync="listQuery.page"
                :limit.sync="listQuery.size"
                @pagination="getList"/>
    <el-dialog title="修改公告" :visible.sync="dialogAddVisible">
      <el-form ref="addForm" :model="addForm" :rules="rule">
        <el-form-item label="公告" prop="content">
          <el-input v-model="addForm.content" ref="content" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddVisible = false">取 消</el-button>
        <el-button type="primary" @click="addBillBoard('addForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {addBillBoardApi, deleteBillBoardApi, getBillBoardListApi, updateBillBoardShowApi} from "../../../api/admin";
import Pagination from "@/components/admin/Pagination";
export default {
  name: "BillBoard",
  components: {Pagination},
  data() {
    return {
      listLoading: true,
      total:0,
      listQuery:{
        page:1,
        size:10
      },
      tableData: [],
      dialogAddVisible: false,
      addForm: {
        content: ''
      },
      rule: {
        content: [
          {required: true, message: '请输入内容', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 1 到 10个字符', trigger: 'blur'}
        ]
      },
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.listLoading = true
      getBillBoardListApi(this.listQuery).then(res => {
        console.log(res)
        let {data} = res
        this.tableData =data.records
        this.total=data.total
        setTimeout(() => {
          this.listLoading = false
        }, 1.5 * 1000)
      })
    },
    deleteRow(index, rows,row) {
      this.$confirm('确定要删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteBillBoardApi(row.id).then(res=>{
          console.log(res)
          rows.splice(index, 1);
          this.total--
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        })
      })
    },
    addBillBoard(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let res =await addBillBoardApi(this.addForm)
          console.log(res)
          this.tableData.unshift(res.data)
          this.addForm.content=""
        } else {
          this.$refs.content.focus()
          return false
        }
      })
    },
    switchControl(status, row) {
      this.$confirm('是否继续操作?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await updateBillBoardShowApi(row.id)
        this.$message({
          type: 'success',
          message: '操作成功!'
        });
      }).catch(() => {
        row.show = !status
      });
    },
  }
}
</script>

<style scoped>

</style>
